import { FormGroup } from '@angular/forms';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http'
import { Observable } from 'rxjs';


import { Colaborador } from '../shared/colaborador';

@Injectable({
  providedIn: 'root'
})
export class ColaboradorService {

  constructor(private http: HttpClient) { }

  baseUrl = 'http://localhost:80/colaborador'


  postColaborador(colaborador: FormGroup): Observable<FormGroup> {
    const cadastrarUrl = `${this.baseUrl}`
    return this.http.post<FormGroup>(cadastrarUrl, colaborador)
  }

  getColaboradores(): Observable<Colaborador[]> {
    const getAllColaborador = this.baseUrl
    return this.http.get<Colaborador[]>(getAllColaborador)
  }

  getColaboradorById(id: string): Observable<Colaborador[]> {
    const getColaboradorId = `${this.baseUrl}/${id}`
    return this.http.get<Colaborador[]>(getColaboradorId)
  }
}
