import { Router } from '@angular/router';
import { Subscription, Observable } from 'rxjs';
import { Component, OnInit, AfterContentInit } from '@angular/core';
import * as M from 'materialize-css'

import { ColaboradorService } from './colaborador-form/colaborador.service';
import { Colaborador } from './shared/colaborador';
import { EmprestimoService } from './emprestimo-form/emprestimo.service';
import { Emprestimo } from './shared/emprestimo';

@Component({
  selector: 'app-emprestimo',
  templateUrl: './emprestimo.component.html',
  styleUrls: ['./emprestimo.component.css']
})
export class EmprestimoComponent implements OnInit {

  emprestimos: Observable<Emprestimo[]>
  inscricao: Subscription
  constructor(
    private emprestimoService: EmprestimoService,
    private router: Router
    ) { }

  ngOnInit() {
    this.emprestimos = this.emprestimoService.getEmprestimosColaborador()
    this.iniciaSelect()
  }

  iniciaSelect() {
    setTimeout(() => {
      var elems = document.querySelectorAll('select');
        var instances = M.FormSelect.init(elems);
      }, 100)
  }

  onNavigate(value) {
    if(value != ""){
      this.router.navigateByUrl(`/consultar/${value}`)
    }
  }
}
