import { FormGroup } from '@angular/forms';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http'
import { Observable } from 'rxjs';

import { Item } from '../shared/item';

@Injectable({
  providedIn: 'root'
})
export class ItemService {

  constructor(private http: HttpClient) { }

  baseUrl = 'http://localhost:80/item'


  postItem(item: FormGroup): Observable<FormGroup> {
    const cadastrarUrl = `${this.baseUrl}`
    return this.http.post<FormGroup>(cadastrarUrl, item)
  }

  getItens(): Observable<Item[]> {
    const getAllItem = this.baseUrl
    return this.http.get<Item[]>(getAllItem)
  }

  getItemById(id: string): Observable<Item[]> {
    const getItemId = `${this.baseUrl}/${id}`
    return this.http.get<Item[]>(getItemId)
  }
}
