import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { EmprestimoComponent } from './emprestimo.component';
import { EmprestimoDetalheComponent } from './emprestimo-detalhe/emprestimo-detalhe.component';
import { EmprestimoFormComponent } from './emprestimo-form/emprestimo-form.component';
import { ColaboradorFormComponent } from './colaborador-form/colaborador-form.component';
import { ItemFormComponent } from './item-form/item-form.component';


const routes: Routes = [
  { path: 'cadastrar', component: EmprestimoFormComponent},
  {path: 'cadastrar/colaborador', component: ColaboradorFormComponent},
  {path: 'cadastrar/item', component: ItemFormComponent},
  { path: 'consultar', component: EmprestimoComponent, children: [
    { path: ':id', component: EmprestimoDetalheComponent}
  ]}  
]

@NgModule({
  imports:[RouterModule.forChild(routes)],
  exports:[RouterModule]
})
export class EmprestimoRoutingModuleModule {}