import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient } from '@angular/common/http'
import { Observable } from 'rxjs';

import { Emprestimo } from '../shared/emprestimo';

@Injectable({
  providedIn: 'root'
})
export class EmprestimoService {

  constructor(private http: HttpClient) { }

  emitirPDF = new EventEmitter()

  baseUrl = 'http://localhost:80/emprestimo'


  postEmprestimo(emprestimo: Emprestimo): Observable<Emprestimo> {
    const cadastrarUrl = `${this.baseUrl}`
    this.emitirPDF.emit(emprestimo)
    return this.http.post<Emprestimo>(cadastrarUrl, emprestimo)
  }

  postEmprestimoDevolvido(emprestimo: []) {
    emprestimo.forEach(element => {
      console.log(element['id_emprestimo']);
      const postEmprestimoDevolvido = `${this.baseUrl}/${element['id_emprestimo']}`
      const obj = { "devolvido": true };     
      this.http.post(postEmprestimoDevolvido, obj).subscribe();
    });
  }

  getEmprestimosColaborador(): Observable<Emprestimo[]> {
    const getAllEmprestimo = `${this.baseUrl}/colaborador`
    return this.http.get<Emprestimo[]>(getAllEmprestimo)
  }

  getEmprestimoById(id: string): Observable<Emprestimo[]> {
    const getEmprestimoId = `${this.baseUrl}/colaborador/${id}`
    return this.http.get<Emprestimo[]>(getEmprestimoId)
  }


}
